select {
    display: inline-block;
    overflow: auto;
    box-sizing: border-box;
    margin: 0;
    padding: 0 36px 0 0;
    padding-left: 0.5em;
    padding-right: 2.8em;
    font-size: 0.8rem; /* set font-size here to avoid 14px from controls.css*/
    vertical-align: middle;
    height: $control-height;
    border: 1px solid #e3e3e3;
    border-radius: 2px;
    outline: none;
    text-overflow: ellipsis;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    cursor: pointer;
    background-image: linear-gradient(45deg, transparent 50%, #707070 50%),linear-gradient(135deg, #707070 50%, transparent 50%);
    background-position: calc(100% - 13px) 13px,calc(100% - 8px) 13px;
    background-size: 5px 5px, 5px 6px;
    background-repeat: no-repeat;
    background-color: #fcfcfc;
    &:focus {
        border-color: $control-hover-border;       
    }
    &:hover {
        border-color: $control-hover-border;
    }
}

select::-ms-expand {
    display: none
}

.theme-night select {
    background-color: #1a1a1a;
    border-color: #e3e3e3;
}

.theme-hot-dog select {
    background-color: #ff2600;
    border-color: #2e2e2e
}

