
.theme-night .hljs {
  display: block;
  overflow-x: auto;
  padding: 0.5em 0.5em 1em 0.5em;
  background: #222324;
  color: #fafcfe;
}

.theme-night .hljs-comment
 {
  color: #57A64A;
 }

.theme-night .hljs-number
{
  color: #b5cea8;
}

.theme-night .hljs-keyword,
.theme-night .hljs-selector-tag,
.theme-night .hljs-built_in,
.theme-night .hljs-name,
.theme-night .hljs-tag {
  color: #569cd6;
}

.theme-night .hljs-literal
{
  color:#569cd6 ;
}
.theme-night .hljs-title
{
  color:#4EC9B0;
}

.theme-night .hljs-string,
.theme-night .hljs-section,
.theme-night .hljs-attribute,
.theme-night .hljs-template-tag,
.theme-night .hljs-template-variable,
.theme-night .hljs-type,
.theme-night .hljs-addition {
  color: #ce9178;
}

.theme-night .hljs-deletion,
.theme-night .hljs-selector-attr,
.theme-night .hljs-selector-pseudo,
.theme-night .hljs-meta {
  color: #2b91af;
}

.theme-night .hljs-doctag {
  color: #808080;
}

.theme-night .hljs-attr {
  color: #9cdcfe;
}

.theme-night .hljs-symbol,
.theme-night .hljs-bullet,
.theme-night .hljs-link {
  color: #808080;
}


.theme-night .hljs-emphasis {
  font-style: italic;
}

.theme-night .hljs-strong {
  font-weight: bold;
}

