$control-height: 32px;
$control-padding: 8px;

@define-mixin control {
  -moz-appearance: none;
  -webkit-appearance: none;
  background: $control-background;
  border: 1px solid $control-border;
  border-radius: $radius;
  color: $control;
  display: inline-block;
  font-size: $size-normal;
  height: $control-height;
  line-height: calc($control-height - $control-padding);
  padding: calc($control-padding / 2) $control-padding;
  position: relative;
  vertical-align: top;

  &:hover {
    border-color: $control-hover-border;
  }
  &:active,
  &:focus {
    border-color: $control-active-border;
    outline: none;
  }
  &[disabled] {
    &,
    &:hover {
      background: $background;
      border-color: $control-border;
      cursor: not-allowed;
    }
  }
}

@define-mixin form-color $name, $color {
  &.is-$(name) {
    border-color: $color;
    color: $color;
  }
}

@define-mixin form-control {
  @mixin control;

  /* Form colors */
  @mixin form-color error, $error;
}

@define-mixin control-with-element {
  cursor: pointer;
  display: inline-display;
  line-height: 16px;
  padding-left: 18px;
  position: relative;
  vertical-align: top;

  & input {
    @mixin form-control;
    border-radius: 1px;
    box-shadow: none;
    cursor: pointer;
    float: left;
    height: 14px;
    left: 0;
    outline: none;
    padding: 0;
    position: absolute;
    top: 1px;
    width: 14px;
  }

  &:hover {
    color: $control-hover;

    & input {
      border-color: $control-hover-border;
      &:checked {
        border-color: $control-active-border;
      }
    }
  }

  &.is-disabled {
    &,
    &:hover {
      color: $text-light;
    }
  }
}


/* Definition of standard HTML form controls */
.input {
  @mixin form-control;
  display: block;
  max-width: 100%;
  width: 100%;
  &[type="search"] {
    /* TODO: Set border-radius */
  }

  &.is-fullwidth: {
    display: block;
    width: 100%;
  }
  &.is-inline {
    display: inline;
    width: auto;
  }
}

.textarea {
  @mixin form-control;
  line-height: 1.2;
  max-height: 600px;
  min-height: 120px;
  max-width: 100%;
  padding: 10px;
  resize: vertical;
  width: 100%;
}

.checkbox {
  @mixin control-with-element;
}

.lang-display {
  @mixin form-control;
  display: inline-block;
  height: $control-height;
  position: relative;
  vertical-align: top;
 
}
.select {
  display: inline-block;
  height: $control-height;
  position: relative;
  vertical-align: top;
  & select {
    @mixin form-control;
    cursor: pointer;
    display: block;
    outline: none;
    padding-right: 36px;
    &:focus {
      border-color: $control-hover-border;
    }
    &:hover {
      border-color: $control-hover-border;
    }
    &::ms-expand {
      display: none;
    }
  }
  &:after {
    @mixin arrow-down $control-border;
    margin-top: -6px;
    right: 16px;
    top: 50%;
  }
  &:hover {
    &:after {
      border-color: $control-hover-border;
    }
  }
}

.label {
  color: $text-strong;
  display: block;

  &.is-capitalized {
    text-transform: uppercase;
  }
}

.control {
  position: relative;
  /* We want to restrict the font-size for the form controls */
  font-size: 0.875rem; /* 14px; */
  text-align: left;

  &.is-horizontal {
    display: flex;
    /* This will make multiple line control such as textarea align correctly */
    align-items: baseline;

    &.on-right {
      justify-content: flex-end;
    }

    & > .button,
    & > .input,
    & > .select {
      &:not(:last-child) {
        margin-right: 10px;
      }
    }
    & > .label {
      margin-right: 10px;
    }
    & > .input,
    & > .textarea,
    & > .select {
      flex: 1;
    }
  }
}
