/* HTML Tags */
html {
  font-size: 100%; /* make 16px as the default font size since a lot related em sizes are used from minky */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  /* overflow-y: scroll; */
  text-rendering: optimizeLegibility;
}

body {
  font-family: $primary-font;
}

code,
kbd,
pre,
samp {
  -moz-osx-font-smoothing: auto;
  -webkit-font-smoothing: auto;
  font-family: $mono-font;
  line-height: 1.25;
}

body {
  background: $body-background;
  color: $text;
  font-size: 1rem;
  line-height: 1.428571428571429;
}

a {
  color: $link;
  cursor: pointer;
  text-decoration: none;
  transition: none $speed $easing;
  &:hover {
    color: $link-hover;
  }
  &:active {
    color: $link-active;
  }
  $:visited {
    color: $link-visited;
  }
}

strong {
  color: $text-strong;
}

small {
  font-size: $size-small;
}

code {
  background-color: $code-background;
  border: solid 1px $border;
  border-radius: $radius;
  /*color: $code;*/
  display: inline-block;
  font-size: 0.875rem; /* 16px * 0.875 = 14px */
  font-weight: normal;
  padding: 1px 2px 2px;
}

pre {
  background-color: $pre-background;
  color: $pre;
  /* CSS 2 */
  white-space: pre;
  /* CSS 2.1*/
  white-space: pre-wrap;
  /* CSS 3 and 2.1 as well */
  overflow-wrap: break-word;
  word-wrap: break-word;
  word-break: break-word;
  hyphens: auto;
  
  & code {
    background-color: $pre-background;
    color: $pre;
    display: block;
    line-height: 1.475;
    overflow-x: auto;
    padding: 12px 16px !important;
  }
}

.theme-night pre code {
  background-color: $pre-dark-background;
  color: $pre-dark
}

/* Generic Extends */
@define-mixin block {
  &:not(:last-child) {
    margin-bottom: 20px;
  }
}

.container {
  position: relative;
  & {
    @mixin desktop {
      margin: 0 auto;
      min-width: 960px;
      &.is-fluid {
        margin: 0 20px;
        min-width: none;
      }
    }
  }
}

dl {
  overflow: hidden;
  position: relative;    
}

dt {    
  float: left;
  width: 2em;
  clear:both;
  position: relative;
}

dd {
  float:right;
}
