#footer #impressum-section {
    display: none;
}

.shell-notification {
    font-weight: normal;
    line-height: 1.5;
    font-family: "Segoe UI","wf_segoe-ui_normal","Arial",sans-serif;
    background-color: #1570A6;
    color: white;
    width: 100%;
    box-sizing: border-box;
}

.shell-notification .shell-notification-grid-row {
    width: 100%;
    box-sizing: border-box;
    position: relative;
    padding: 0 5px;
    max-width: 1600px;
}

.shell-notification .notification-txt {
    float: left;
    padding-right: 5px;
    margin-right: 5px;
    margin-bottom: 4px;
}

.shell-notification .notification-action {
    float: right;
    padding-left: 5px;
    margin-left: 5px;
}
.shell-notification .shell-notification-clearfix:after {
    content: "";
    display: table;
    clear: both;
}

.shell-notification a {
    color:red;
}

.shell-notification a, .shell-notification a:hover, .shell-notification a:focus, .shell-notification a:link, .shell-notification a:visited {
    display: inline-block;
    color: white;
    text-decoration: underline;
}
.shell-icon-close {
    position: relative;
    width: 15px;
    height: 15px;
    top: 32%;
    margin-left: 4px;
    background: url(../images/close.png) 50% 50% no-repeat;
}

@media (min-width: 899px) {
    .shell-notification .shell-notification-grid-row {
        width: 90%;
        margin: 0 auto;
        padding: 0;
    }
}

.shell-notification i[class*="shell-icon-close"] {
    display: inline-block;
    width: 1em;
    height: 1em;
    vertical-align: middle;
    vertical-align: -3px;
    overflow: visible;
    font-size: 1em;
    margin-left: 11px;
}
.theme-night .shell-notification a:visited, .theme-night .shell-notification a:hover {
    color: white;
}