@define-mixin content {
  @mixin block;
  & h1,
  & h2,
  & h3,
  & h4,
  & h5,
  & h6 {
    font-weight: 300;
    line-height: 1.125;
    margin-bottom: 0.5em;
    margin-top: 0.25em;
    word-wrap: break-word;
  }
  & h1,
  & h2,
  & h3 {
    &:not(:first-child) {
      margin-top: 40px;
    }
  }
  & h1 {
    font-size: 2em;
    &:not(:first-child) {
      margin-top: 1.25em;
    }
  }
  & h2 {
    font-size: 1.75em;
    margin-top: 0.75em;
  }
  & h3 {
    font-size: 1.5em;
  }
  & h4 {
    font-size: 1.25em;
  }
  & h5 {
    font-size: 1.125em;
  }
  & h6 {
    color: #666;
    font-size: 1em;
    font-weight: 600;
    letter-spacing: 1px;
    text-transform: uppercase;
  }
  & p{
    line-height: 1.55;

    &:not(:last-child) {
      margin-bottom: 1em;
    }
  }
  li {
    line-height: 1.62;
  }
  li + li {
    margin-top: 0.85em;
  }

}

