/* Segment is a divided list of items, but not complex enough to use table */
/* TODO: Deprecate member-details */
ul {
  &.member-details,
  &.segment {
    @mixin block;
    list-style-type: none;
    background-color: $background;
    border: solid 1px $border;

    padding: 0;
  }
}

.member-details {
  &>li {
    padding: 8px;
    &:not(:last-child) {
      border-bottom: solid 1px $border;
    }
  }

  & ol.meta {
    display: flex;
    list-style-type: none;
    justify-content: space-between;
    padding: 0;
    margin-bottom: 0.7em;

    & li {
      /* This is hacky, should wrap the property value with strong tag */
      &:last-child {
        & code {
          font-weight: 300;
        }
      }

      &:first-child,
      &:only-child {
        & code {
          font-weight: 600;
        }
      }
    }

    & code {
      background-color: transparent;
      border: none;
    }
  }

  & .description {
    clear: right;

    & p {
      margin: 0.3em 0;
    }
  }

  & .child-toggle {
    position: relative;
    margin: 0.6em 0 0.4em;
    padding-right: 14px;

    &:after {
      @mixin arrow-down $link;
      margin-top: -5px;
      right: 0px;
      top: 50%;
    }
    &:hover {
      &:after {
        border-color: $link-hover;
      }
    }

    &.expanded {
      &:after {
        @mixin arrow-up $link;
        margin-top: 0;
      }
      &:hover {
        &:after {
          border-color: $link-hover;
        }
      }
    }
  }

  & li + li {
    margin: 0;
  }
}
