$control-height: 32px;

.navbar-item {
    display: inline-block;

    & .title,
    & .subtitle {
        margin-bottom: 0;
    }

    & {
        @mixin mobile {
            &:not(:last-child) {
                margin-bottom: 10px;
            }
        }
    }

    /* Reset the p margin */
    & > p {
        margin: 0;
    }
}

.navbar {
    @mixin block;
    /* To enable using ul>li within navbar */
    & > ul {
        list-style-type: none;
        margin: 0;
        padding: 0px 0px 12px 0px;
        & > li {
            list-style-type: none;
        }
    }
}

.navbar-left,
.navbar-right {
    & .navbar-item {
        &.is-flexible {
            flex: 1;
        }
        &:not(:last-child) {
            margin-right: 10px;
        }
    }
}

.navbar-left {
    display: inline-block;
    vertical-align: middle;
    flex-shrink: 0;
    padding-bottom: 12px;
    & {
        @mixin mobile {
            & + .navbar-right {
                margin-top: 20px;
            }
        }

        @mixin tablet {
            height: $control-height;
            &:not(:last-child) {
                margin-right: 20px;
            }
        }
    }
}

.navbar-right {
    & {
        @mixin tablet {
            align-items: center;
            display: inline-flex;
            justify-content: flex-end;
        }
    }
}