/* Common CSS for conceptual and reference. */

@import "font";

div.button a, div.step-by-step a {
    position: relative;
    display: inline-block;
    font-size: 0.7em;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-weight: 600;
    vertical-align: top;
    padding: 0.2em 0.7em;
    border-radius: 2px;
    border: 0;
    background-color: #007bb8;
    color: #fcfcfc
}

.theme-night div.button a, .theme-night div.step-by-step a {
    background-color: #007bb8;
    color: #1a1a1a
}

.theme-hot-dog div.button a, .theme-hot-dog div.step-by-step a {
    background-color: #fefb00;
    color: #ff2600
}

div.button a:focus, div.step-by-step a:focus {
    background-color: #0070a7;
    color: #f5f5f5
}


div.button a:hover, div.step-by-step a:hover {
    background-color: #0070a7;
    color: #f5f5f5
}

.theme-night div.button a:hover, .theme-night div.step-by-step a:hover {
    background-color: #0070a7;
    color: #242424;
}

.theme-hot-dog div.button a:hover, .theme-hot-dog div.step-by-step a:hover {
    background-color: #b2af00;
    color: #ff3714;
}

div.step-by-step a {
    margin: 0.1em 0.5em;
}

.theme-night div.step-by-step {
    border-color: #2e2e2e;
}

.theme-hot-dog div.step-by-step {
    border-color: #2e2e2e
}

div#headerWrapper {
    height: 96px;
}

#sub-header ul.headerlink {
    float:right;
    height:auto;
    padding:0em 1em; 
    font-size: 0.8em;
    color:black;
    position: relative;
    margin: 0;
}

#sub-header ul.headerlink li {
    display: inline-block;
    margin: 0;
}

/*Override open-feedback ui styles*/
.ofb-container.ofb-child-vote, .ofb-container>.ofb-vote, .ofb-container.ofb-child-thanks {
    height: 44px !important;
}

@media only screen and (max-width: 500px) {
    .ofb-container.ofb-child-vote, .ofb-container>.ofb-vote {
        height: 80px !important;
    }
}

.ofb-container>.ofb-feedback, .ofb-container>.ofb-vote {
    background-color: #007bb8 !important;
}

.ofb-container>.ofb-vote .ofb-content {
    padding: 8px 0 0 0 !important;
}

.ofb-container, .ofb-main-title {
    font-size: 13px !important;
}

.ofb-container button, .ofb-container>.ofb-feedback>.ofb-form .ofb-charcnt, .ofb-container>.ofb-thanks .ofb-appreciate, .ofb-secondary-title {
    font-size: 11px !important;
}

.ofb-container button {
    height: 28px !important;
    border-radius: 0px !important;
}

.ofb-container.ofb-child-feedback, .ofb-container>.ofb-feedback {
    height: 175px !important;
}

@media only screen and (max-width: 652px) {
    .ofb-container.ofb-child-feedback, .ofb-container>.ofb-feedback {
        height: 215px !important;
    }
}

.ofb-container>.ofb-feedback>.ofb-form>textarea {
    font-size: 13px !important;
    height: 80px !important;
}

.ofb-container > .ofb-thanks {
    height: 44px !important;
    background-color: #007bb8 !important;
}

.ofb-container>.ofb-thanks .ofb-content {
    padding: 10px 0 0 0 !important;
}

.ofb-container, .ofb-container>.ofb-feedback>.ofb-form>textarea, .ofb-ui-font {
    font-family: wf_segoe-ui_normal,"Segoe UI","Segoe WP",Tahoma,Arial,sans-serif !important;
}
/*END: Override open-feedback ui styles*/


/* BEGIN: Override open-feedback ui styles (New Design) */
.ofb-container-var111 button,
.ofb-container-var112 button,
.ofb-container-var113 button,
.ofb-container-var222 button {
    font-family: wf_segoe-ui_normal,"Segoe UI","Segoe WP",Tahoma,Arial,sans-serif !important;
    font-size: 0.63rem !important;
    letter-spacing: 1px !important;
    font-weight: 600 !important;
    padding: 0.2em 0.7em !important;
    line-height: 25.6px !important;
}
/* END: Override open-feedback ui styles (New Design) */


/* Make hidden attribute compatible with old browser.*/
[hidden] {
    display: none !important;
}

/* Skip link for keyboard navigation */
#skiplink a {
    position: absolute;
    left: -10000px;
    right:auto;
    top:auto;
    padding: .4em;
    background: white;
    border: #007bb8;
    border-style: solid;
    font-size: 0.8em;
    border-width: 0.1em;
    margin: 0.4em;
    opacity: 0;
    -webkit-transition: opacity 0.2s linear 0.2s;
    -moz-transition: opacity 0.2s linear 0.2s;
    transition: opacity 0.2s linear 0.2s;
}
#skiplink a:focus {
    position: absolute;
    top: 0;
    left:auto;
    right: 0;
    z-index: 1;
    opacity: 1;
    -webkit-transition: opacity 0.2s linear 0s;
    -moz-transition: opacity 0.2s linear 0s;
    transition: opacity 0.2s linear 0s;
}

.breadcrumbs li:after {
    content: '\\';
    vertical-align: baseline;
    padding: 0 0.5em;
    color: #ababab
}

/* Global Search CSS */
#search-menu{
    float:right;
    margin-right:1em;
    font-size: 0.87em;
}

#search-input{
    padding-left:4px;
}

/* Container and iFrame styles for embedded video */
div.embeddedvideo {
    padding-top: 56.25%; 
    position: relative; 
    width: 100%;
}

div.embeddedvideo iframe {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
}
