$reference-toc-width: 226px;
$reference-body-left: 227px;
$reference-padding: 20px;


/* Navbar */
.reference-nav {
    padding: 12px 20px 0px 20px;
}
    /* Article */
.reference-content {
  @mixin content;

  /* ~16px */
  font-size: 1rem;
  padding-left: $reference-padding;
  padding-right: $reference-padding;

  & h6 {
    font-size: 0.7em;
    margin-top: 20px;
  }

  & .inheritance {
    margin-bottom: 12px;
  }

  & .alert {
    background: $white;
    border: 1px solid color($border alpha(50%));
    padding: 12px;

    & li {
      margin-top: 0;
    }

    & h5 {
      color: $text-strong;
      font-size: 0.7em;
      font-weight: 800;
      letter-spacing: 0.65px;
      margin: 0.35em 0;
      text-transform: uppercase;
    }

    & p {
      margin: 0;
    }

  }
}

/* Use position model to layout the toc sidebar and body area */
.reference {
  box-sizing: border-box;
  position: relative;
  overflow: auto;

  & .reference-toc {
    background: $background;
    float: left;
    width: $reference-toc-width;
    overflow-y: auto;
  }

  .reference-body-outer {
    margin-left: $reference-body-left;
    position: relative;
  }

  & .reference-body {
    float: left;
    width: 100%;
    overflow: hidden;
  }
}

/* ------ */
/* TODO: Will deprecate when we start to touch the rendered content area */
.reference-body {
    & .header,
    & .level {
      @mixin grids;
      margin-bottom: 0;

      & > .primary,
      & > .secondary {
          box-sizing: border-box;
          max-width: 50%;
          padding: 1px;
      }

        /* Set flex-shrink 0 to primary to avoid expanded right side code snippet*/
        /* when there is scrollbar available */
        & > .primary {
            flex: 1 0 auto;
            width: 45%;
            padding-right: 16px ;
        }
        & > .secondary {
            flex: none;
            padding-left: 16px;
            width: 55%;
        }
    }
}


@media only screen and (max-width: $tablet) {
    .reference .reference-body-outer {
        margin-left: 0px;
        position: relative;
    }
    .reference-body {
        & .header,
        & .level {
            display:block;
            margin-bottom: 0;
            padding: 1px;
            margin: 0px;
            & > .primary,
            & > .secondary {
                max-width: 100%;
            }

            & > .primary {
                display:block;
                padding-right: 1px;
                width: 100%;
            }
            & > .secondary {
                display:block;
                padding-left: 1px;
                width: 100%;
            }

        }
    }
}


.list-clean {
  list-style-type: none;
  padding: 0;
}

.expand-container {
    /* Class name*/
    & .expander {
        position: relative;
        padding-right: 14px;

        & code {
            background: transparent;
            border: none;
        }

        &:after {
            @mixin arrow-down $link;
            margin-top: -3px;
            right: 0;
            top: 50%;
        }
        &:hover {
            &:after {
                border-color: $link-hover;
            }
        }

        &.expanded {
      &:after {
        @mixin arrow-up $link;
        margin-top: 3px;
      }
      &:hover {
        &:after {
          border-color: $link-hover;
        }
      }
    }
    }

    /* Expandable content */
    & .content {
        background: $white;
        border-top: 1px solid $background;
        border-bottom: @border-top;
        margin-left: 0px;
        margin-right: 0px;
        padding: 2px;
    }
}

.theme-night .expand-container {
    & .content {
      background: transparent;
      border-color: transparent;
    }
}